.apart {
    padding-top: 100px;
    padding-bottom: 130px;
    position: relative;
    &__in {
        display: inline-block;
        min-width: 230px; }
    &__col {
        display: inline-block;
        &_l {
            width: percentage(230/1000); }
        &_m {
            width: percentage(425/1000); }
        &_r {
            width: percentage(330/1000);
            text-align: right; } }
    &__mini-col {
        display: inline-block;
        &_l {
            text-align: left;
            width: percentage(145/230); }
        &_r {
            text-align: right;
            width: percentage(80/230); } }
    &__ttl {
        @include helv-r;
        color: $gold;
        font-size: 18px;
        text-transform: uppercase;
        margin-top: 42px;
        margin-bottom: 35px; }
    &__types {
        margin-bottom: 110px; }
    &__type {
        display: block;
        background-color: transparent;
        @include helv-m;
        color: $gray;
        text-decoration: underline;
        line-height: 2;
        &:hover, &.is-active {
            color: $gold; } }
    &__metrics {
        display: inline-block;
        padding-top: 48px;
        padding-bottom: 30px;
        min-width: 100%;
        font-size: 12px;
        display: none;
        &.is-active {
            display: block; } }
    &__rooms {
        li {
            list-style-type: decimal-leading-zero;
            list-style-position: inside; } }
    &__total {
        margin-top: 30px; }
    &__links {
        @include helv-m; }
    &__link {
        display: block;
        line-height: 2.3;
        text-align: left;
        &_red {
            color: $red; }
        &_black {
            color: $black; } } }

.apart-menu {
    &__item {
        display: inline-block;
        margin-right: 40px; }
    &__ttl {
        padding: 15px 0; }
    &__btn {
        background-color: transparent;
        border: 1px solid $gray;
        border-radius: 1px;
        height: 40px;
        margin-right: 6px;
        @include helv-m;
        color: $gray;
        &:hover, &.is-active {
            color: #fff;
            background-color: $gray; }
        &_lg {
            width: 150px; }
        &_sm {
            width: 50px; } } }

.apart-content {
    display: none;
    &.is-active {
        display: block; }


    &__desc {
        max-width: 150px;
        font-size: 12.6px;
        display: none;
        &.is-active {
            display: block; } }
    &__img {
        width: 100%;
        margin-bottom: -54px;
        cursor: pointer;
        display: none;
        img {
            max-width: 100%; }
        &.is-active {
            display: block; } } }


.apart-popup {
    width: 100%;
    height: 100vh;
    background-color: #fff;
    @include vertical;
    &__in {
        display: inline-block;
        vertical-align: middle;
        @include vertical;
        text-align: left;
        padding: 40px; }
    &__img {
        display: inline-block;
        width: percentage(1153/1920);
        vertical-align: middle;
        img {
            max-width: 100%; } }
    &__txt {
        display: inline-block;
        width: percentage(767/1920);
        min-width: 320px;
        vertical-align: middle;
        padding-left: 110px;
        font-size: 18px; }
    &__ttl {
        font-size: 24px;
        margin: 0; }
    &__type {
        @include helv-r;
        color: $gold;
        font-size: 24px;
        text-transform: uppercase;
        text-decoration: none;
        line-height: 1.4; }
    &__metrics {
        display: inline-block;
        font-size: 18px;
        padding-top: 30px;
        @include r($desktop-s) {
            font-size: 16px; } }
    &__total {
        @include helv-m;
        color: #b8aa86; }
    &__link {
        @include helv-m;
        font-size: 18px;
        @include r($desktop-s) {
            font-size: 16px; } } }
.icon-cancel-gold {
    @include s(cancel-gold);
    display: block; }



